import React from 'react';
import './App.css';
import Loadable from "react-loadable";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Privacy from './components/policy/privacy';
import Terms from './components/policy/terms';
import Refund from './components/policy/refund';


function App() {

  const loading = (
    <div style={{display:"flex",flexDirection:"column" ,justifyContent:"center",alignItems:"center"}}>
    <img src="https://media.giphy.com/media/MZ9cQ7JpR7iDIlSUvm/giphy.gif" style={{width:"30%"}}/>
    <div style={{ fontSize:"40px",color:"#189AB4",fontWeight:"bold" }}>Loading...</div>
 </div>
  );

  const Home = Loadable({
    loader: () => import("./Pages/Home/index"),
    loading: () => loading,
  });
  const Staff = Loadable({
    loader: () => import("./Pages/Staff/index"),
    loading: () => loading,
  });
  const StudentResult = Loadable({
    loader: () => import("./Pages/StudentResult/index"),
    loading: () => loading,
  });
  const Privacy = Loadable({
    loader: () => import("./Pages/policy/privacy"),
    loading: () => loading,
  });
  const Terms = Loadable({
    loader: () => import("./Pages/policy/privacy"),
    loading: () => loading,
  });
  const Refund = Loadable({
    loader: () => import("./Pages/policy/privacy"),
    loading: () => loading,
  });
    
  
  return(
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/faculty" component={Staff} />
        <Route exact path="/studentresult" component={StudentResult} />
        <Route exact path="/privacy-policy" component={Privacy} />
        <Route exact path="/terms-of-service" component={Terms} />
        <Route exact path="/refund-policy" component={Refund} />
      </Switch>
    </BrowserRouter>
  )
}
export default App;
